import React, { useState, useContext, useEffect } from "react";
import { GlobalState } from "../../GlobalState";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronDown } from "@fortawesome/free-solid-svg-icons";

const Welcome = (props) => {
  const state = useContext(GlobalState);
  const [scroll] = state.scroll;
  const [minHeight, setMinHeight] = useState("100vh");
  useEffect(() => {
    if (window.innerWidth < 771) {
      setMinHeight(window.innerHeight);
    }
  }, []);

  return (
    <div
      id="welcome-section"
      className="welcome-section-container"
      style={{ height: minHeight + minHeight * 0.07 }}
    >
      <div className="welcome-container">
        <div
          className="text-container"
          style={
            scroll === "welcomeFade"
              ? { opacity: 1 - (scroll / 1000) * 2 }
              : { opacity: 1 }
          }
        >
          <h1>Federico Fridman </h1>
          <div className="line-container">
            <div>
              <hr />
            </div>
          </div>
          <p>FULL-STACK WEB DEVELOPER</p>
        </div>
      </div>
      <a href="#about-section" className="subtext-container">
        <p className="subtext">LEARN MORE</p>
        <span>
          <FontAwesomeIcon icon={faChevronDown} color="#f2f2f2" />
        </span>
      </a>
    </div>
  );
};

export default Welcome;
