import React, { useState } from "react";
import GithubLogo from "./github.png";
import DownloadLogo from "./download.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFileDownload } from "@fortawesome/free-solid-svg-icons";

const Separator = (props) => {
  const [hoverOn, setHoverOn] = useState(false);
  let message;
  if (props.separator === "works") {
    message = "SEE MORE PROJECTS ON GITHUB";
  } else if (props.separator === "cv") {
    message = "VIEW MY FULL RESUME";
  }
  return (
    <div
      className={hoverOn ? "onHover cv-container" : "cv-container"}
      onMouseEnter={() => setHoverOn(true)}
      onMouseLeave={() => setHoverOn(false)}
    >
      <a
        href={props.separator === "works" ? "google.com/git" : "google.com/cv"}
      >
        {props.separator === "works" ? (
          <p>
            {hoverOn ? (
              <img src={GithubLogo} className="github-logo" alt="github" />
            ) : (
              message
            )}
          </p>
        ) : (
          <p>
            {hoverOn ? (
              <FontAwesomeIcon
                icon={faFileDownload}
                color="#f2f2f2"
                size="3x"
              />
            ) : (
              message
            )}
          </p>
        )}
      </a>
    </div>
  );
};

export default Separator;
