import React, { useState } from "react";

const MobileNav = () => {
  const [navActive, setNavActive] = useState(false);

  return (
    <section className="mobile-nav-container">
      <div className={navActive ? "sections active" : "sections"}>
        <div>
          <a
            id="news"
            href="#about-section"
            onClick={() => setNavActive(!navActive)}
          >
            About
          </a>
          <a href="#services-section" onClick={() => setNavActive(!navActive)}>
            Services
          </a>
          <a href="#works-section" onClick={() => setNavActive(!navActive)}>
            Works
          </a>
          <a href="#contact-section" onClick={() => setNavActive(!navActive)}>
            Contact
          </a>
        </div>
      </div>
      <div
        className="mobile-nav-lines"
        onClick={() => setNavActive(!navActive)}
      >
        <div className="lines" id={navActive ? "line1" : "line1no"}></div>
        <div className="lines" id={navActive ? "line2" : "line2no"}></div>
        <div className="lines" id={navActive ? "line3" : "line3no"}></div>
      </div>
    </section>
  );
};

export default MobileNav;
